import { Routes, Route } from 'react-router-dom';
import { GameScreen } from './components/MainGameComponents/GameScreen';
import { Main } from './components/Main/Main';
import { Register } from './components/Register/Register';
import './App.css';
import { Connect } from './components/Connect/Connect';

export const App: React.FC = () => (
  <Routes>
    <Route path="/scrum-poker"  element={<Main />} />
    <Route path="/scrum-poker/register"  element={<Register />} />
    <Route 
      path="/scrum-poker/game"  
      element={
        <Connect loginClick={() => window.close()} />
      }
    />
    <Route path="/scrum-poker/game/:boardId" element={<GameScreen />} />
    <Route path="*" element={<Main />} />
  </Routes>
);
